import React from 'react'
import regulamin from '../../images/regulamin.svg'
import school from '../../images/school.svg'
import './regulations.min.css'

const Regulations = () => {
    return (
        <div>
            <br/>
            <br/>
            <div class="tekst1">
                <h1>Regulamin</h1>
            </div>
            <div class="tekst2">
                <h3>Naszym zadaniem jest zapewnienie Tobie, przecitnemu konsumentowi wygodnego i
                <br/>szybkiego narzdzia dajcego dostp do ofert spersonalizowanych specjalnie dla ciebie.
                </h3>
            </div>
            <div class="logo2142">
                <img src={regulamin} alt="Screen"/>
            </div>


    <div class="regulations">
        <h1>Postanowienia Oglne.</h1>
        <h3>
            Administracja Serwisu dokada wszelkich stara by na naszej platformie panowaa
            pozytywna atmosfera i aby aden uytkownik nie czu si na platformie le. 
            Dlatego zasady korzystania z naszej platformy okrela Regulamin Serwisu 
            (zwany dalej Regulaminem). Administracja Serwisu (zwana dalej Administracj)
            zastrzega sobie prawo do zmiany Regulaminu.
            I. Platforma internetowa - Co Za 500 Plus (Zwana dalej Serwisem).
        </h3>
            
        <i>
            <p>
             1. Serwis ma za zadanie umoliwi uytkownikom szybki dostp do 
            spersonalizowanych ofert zakupowych (zwanych dalej Okazjami). Korzystanie
             z Serwisu jest nieodpatne poza sytuacjami wskazanymi w Regulaminie.
             </p>
        </i>
        
        
        
        <p>
             2. Serwis nie jest powizany z adn parti polityczn, ani
             z programem Rodzina 500+. II. Uytkownik i konto.
        </p>
        
        <p>
             1. Kady uytkownik Serwisu (zwany dalej Uytkownikiem) zobowizany jest
            do przestrzegania Regulaminu.
        </p>
        
            <p> 2. Administracja zastrzega sobie prawo do naoenia blokady na konto Uytkownika, jeli uzna e Uytkownik amie postanowienia Regulaminu.</p>
        
                <p> 3. Wszystkich Uytkownikw obowizuje zakaz sprzeday kont utworzonych na Serwisie. Zakazane jest take przekazywianie przez Uytkownikw kont utworzonych na Serwisie na czyjkolwiek rzecz oraz posugiwanie si cudzymi kontami. W razie wykrycia takiego procederu przez Administracj, konta wszystkich Uytkownikw biorcych udzia w w/w procederze zostan zablokowane.</p>
        
                    <p>  4. W celu ochrony Serwisu przed cyberprzestpczoci zabrania si tworzenia kont przy pomocy VPN (Virtual Private Network, wirtualna sie prywatna) bd tworzenia ich poprzez uycie sieci TOR (The Onion Router).</p>
        
                        <p> 5. Kady Uytkownik zobowizany jest zaoy konto uywajc prawdziwych i jego wasnych danych osobowych i aktualnego adresu e-mail do ktrego ma legalny dostp. Jest to konieczne m.in. ze wzgldu na ewentualn potrzeb kontaktu Administracji z Uytkownikiem.</p>
        
                            <p>  6. Uytkownik ma moliwo:</p>
        
                                <p>Oceniania Okazji dodawanych przez algorytm.</p>
        
        <p> Komentowania Okazji oraz dyskutowania na ich temat. 
        Korzystania z Okazji udostpnianych na Serwisie.</p>
    <h2>III. Treci zabronione.</h2>
        
        <p> 1. Podczas korzystania z Serwisu zabronione jest:</p>
        
        <p>zamieszczanie przez Uytkownikw treci zabronionych przez Polskie prawo; Administracja zastrzega sobie moliwo zgoszenia takiego zachowania do organw cigania,</p>
        
        <p>umieszczanie przez Uytkownikw treci powszechnie uwaanych za spam jak np. reklamy lub linki prowadzce do treci niezwizanych tematycznie z Serwisem/prowadzon dyskusj,</p>
        
        <p>tworzenie przez Uytkownikw sieci afiliacyjnych czy innych form nieautoryzowanej sprzeday,</p>
        
        <p>tworzenie i promocja piramid finansowych, a take namawianie do udziau w piramidach finansowych,</p>
        
        <p>zamieszczanie treci pornograficznych bd innych powszechnie uwaanych za niesmaczne, odrzucajce,</p>
        
        <p>
             zamieszczanie treci bdcych lub mogcych by uznane za autopromocje lub rekomendacje, w 
             szczeglnoci przy jednoczesnym czerpaniu z tego tytuu korzyci, wczajc w to rekomendacje
             lub promocje: pracownikw, rodziny, znajomych, sieci afiliacyjnych, agencji marketingowych 
             itp., ankiet, akcji charytatywnych, konkursw, prywatnych aukcji, linkw do stron wasnych 
             lub trzecich, reflinkw, kodw polecajcych, reklam; a take zamieszczanie prywatnego adresu
            e-mail i ofert sprzeday  bez uprzedniej wyranej zgody Administracji,
        </p>
        
        <p>wykorzystywanie luk w zabezpieczeniach Serwisu oraz wszelka pomoc w takich dziaaniach,</p>
        
        <p>zamieszczanie zoliwego oprogramowania oraz linkw prowadzcych do takiego oprogramowania,</p>
        
        <p>uywanie w stosunku do innych Uytkownikw (a take Administracji) zwrotw obraliwych.</p>
        <h2>IV. Administracja.</h2>
        <p> 1. Administracja dba o przestrzeganie niniejszego Regulaminu.</p>
        
        <p> 2. Podczas dziaania Administracja stosuje wedle uznania i w dowolnej kombinacji rne narzdzia i kary.</p>
        <p> 
        Kary i narzdzia stosowane przez Adminstracj to m.in.:
        upomnienie,
        usunicie treci,
        czasowe ograniczenie moliwoci korzystania z Serwisu (nazywane blokad tymczasow),
        trwae ograniczenie moliwoci korzystania z Serwisu (nazywanie blokad permamentn),
        ograniczenie moliwoci wywietlenia treci Uytkownika przez innych Uytkownikw (nazywane shadow banem).
        </p>
        
        <p>Uytkownikowi przysuguje prawo do odwoania si od naoonej kary poprzez kontakt z Administracj. Ostateczna decyzja Administracji jest wica i nie podlega dyskusji.</p>
        
        <p>Zabrania si omijania naoonych kar poprzez tworzenie nowych kont (tzw. multikont).</p>
            
        <p>  3. Kontakt z Administracj mona nawiza za pomoc formularza kontaktowego na stronie w sekcji "kontakt".</p>
        
        <i>
            <p>
             4. Kady Uytkownik ma prawo poinformowa Administracj o nieodpowiednim
             komentarzu bd innej treci, jeli ma podejrzenie i ta amie Regulamin. 
             Naduywanie tego prawa (np. poprzez spam zgoszeniami) moe prowadzi to 
             ograniczenia tej moliwoci.
            </p>
        </i>      
    </div>
    <section class="grid purples why-start">
        <div class="inner">
            <div class="circle">
            <img src={school} alt="Screen"/>
            </div>
            <div>
                <h1>Dlaczego warto zacz?</h1>
                <h3>Naszym priorytetem jest, byo i bdzie zapewnienie uzytkownikowi jak
                najwikszego komfortu podczas korzystania z naszego serwisu.</h3>
            </div>
        </div>
    </section>
</div>
        
    )
}

export default Regulations
